export async function getFitnessData() {
    const access_token = sessionStorage.getItem('access_token');
    if (!access_token) {
      throw new Error('Access token is missing');
    }
  
    const now = new Date();
    const startOfDay = new Date(now.getFullYear(), now.getMonth(), now.getDate());
    const timestamp = startOfDay / 1;
  
    const data = JSON.stringify({
      aggregateBy: [{
        dataTypeName: "com.google.step_count.delta",
        dataSourceId: "derived:com.google.step_count.delta:com.google.android.gms:estimated_steps"
      }],
      bucketByTime: { durationMillis: now - timestamp },
      startTimeMillis: timestamp,
      endTimeMillis: now.getTime(),
    });
  
    const response = await fetch("https://www.googleapis.com/fitness/v1/users/me/dataset:aggregate", {
      method: "POST",
      headers: {
        "Authorization": "Bearer " + access_token,
        "Content-Type": "application/json"
      },
      body: data
    });
  
    if (!response.ok) {
      throw new Error('Failed to fetch the step count data');
    }
  
    const responseData = await response.json();
    let steps = 0;
    if (responseData.bucket && responseData.bucket.length > 0) {
      const dataset = responseData.bucket[0].dataset[0];
      if (dataset.point && dataset.point.length > 0) {
        steps = dataset.point[0].value[0].intVal;
      }
    }
  
    return steps;
}
